/* eslint-disable react/no-direct-mutation-state */
import React, { Component } from "react";
import { Navigate } from "react-router-dom";
import FormName from "../components/FormNameUpdatePage";
import Wrapper_ClickFun from "../components/wrapperClickFun";
import ChangeHandler from "../components/ChangeHandler";
import Loader from "../components/Loader";
import SubmitButton from "../components/submitButton";
import ErrorMessage from "../components/errorMessage";
import SaveSubmitFun from "../components/saveSubmitFun"
import getDetailsFun from "../components/getDetailsFun";

const API_form_name = "customerMaster";
const List_Path = `/${process.env.REACT_APP_NAME}/${API_form_name}`;
const form_name = "Customer Master";
const form_type = "Master";

const StateDic = {
    details: "",
    obj_id: "",
    cust_code: "",
    cust_desc: "",
    status: "1",
    err_msg_visible: false,
    error_message: "",
    loading: true,
    redirect: false,
};

class CreateUpdatePage extends Component {
    constructor(props) {
        super(props);
        this.state = StateDic;
    }

    drpHandleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value, });
    };

    getDetails = async () => {
        this.setState({ loading: true });
        const queryParams = window.location.pathname;
        await getDetailsFun.call(this, queryParams, form_name)
        if (this.state.details !== "") {
            this.setState({
                obj_id: this.state.details.id,
                cust_code: this.state.details.cust_code,
                cust_desc: this.state.details.cust_desc,
                status: this.state.details.status === true ? "1" : "0",
                loading: false,
            })
        }
    };

    componentDidMount() {
        this.getDetails();
    }

    handleSubmit = async (event) => {
        event.preventDefault();
        var url = `${List_Path}/CreateUpdateAPI/`
        SaveSubmitFun.call(this, event, url)
    };

    render() {
        const { error_message, redirect } = this.state;

        if (redirect) {
            return <Navigate to={List_Path} />;
        }

        return (
            <div className="wrapper">
                <div className="content-wrapper" onClick={Wrapper_ClickFun.bind(this)}>
                    <form id="app-form" onSubmit={this.handleSubmit} autoComplete="off">
                        <FormName
                            form_name_list={form_name}
                            form_name={form_name}
                            form_type={form_type}
                            list_page={List_Path}
                        ></FormName>
                        <Loader load={this.state.loading}></Loader>
                        <ErrorMessage
                            error_message={error_message}
                            err_msg_visible={this.state.err_msg_visible}
                        ></ErrorMessage>
                        <div className="row">
                            <p style={{ margin: "0px", fontSize: "15px" }}>Customer Details</p>
                            <hr style={{ margin: "0px", marginBottom: "3px", height: "0.6px", borderColor: "#021323" }} />
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="cust_code"
                                    id="cust_code"
                                    value={this.state.cust_code}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                    readOnly={true}
                                    disabled={true}
                                />
                                <label className="did-floating-label">Customer Code</label>

                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="cust_desc"
                                    id="cust_desc"
                                    required
                                    value={this.state.cust_desc}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Customer Name</label>
                                <div className="form-helper helper_hide" id="cust_desc_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="legal_name"
                                    id="legal_name"
                                    required
                                    value={this.state.legal_name}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Legal Name</label>
                                <div className="form-helper helper_hide" id="legal_name_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg- col-md-3 col-sm-3 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="cin_number"
                                    id="cin_number"
                                    required
                                    value={this.state.cin_number}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">CIN Number</label>
                                <div className="form-helper helper_hide" id="cin_number_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="pan_number"
                                    id="pan_number"
                                    required
                                    value={this.state.pan_number}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">PAN Number</label>
                                <div className="form-helper helper_hide" id="pan_number_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="fin_code"
                                    id="fin_code"
                                    required
                                    value={this.state.fin_code}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Finance Code</label>
                                <div className="form-helper helper_hide" id="fin_code_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <select
                                    className="did-floating-select"
                                    name="payment_terms"
                                    id="payment_terms"
                                    value={this.state.payment_terms}
                                    onChange={this.drpHandleChange}
                                    onClick={this.drpHandleChange}
                                    onFocus={this.drpHandleChange}
                                    required
                                >
                                    <option value=""></option>
                                    <option value="0">Immediate</option>
                                    <option value="30">30 Days</option>
                                    <option value="60">60 Days</option>
                                    <option value="90">90 Days</option>
                                </select>
                                <label className="did-floating-label">Payment Terms</label>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="number"
                                    placeholder=" "
                                    name="credit_limit"
                                    id="credit_limit"
                                    required
                                    value={this.state.credit_limit}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Credit Limit</label>
                                <div className="form-helper helper_hide" id="credit_limit_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <select
                                    className="did-floating-select"
                                    name="status"
                                    id="status"
                                    value={this.state.status}
                                    onChange={this.drpHandleChange}
                                    onClick={this.drpHandleChange}
                                    onFocus={this.drpHandleChange}
                                >
                                    <option value="1">Active</option>
                                    <option value="0">InActive</option>
                                </select>
                                <label className="did-floating-label">Status</label>
                            </div>
                            <p style={{ margin: "0px", fontSize: "15px" }}>Communication Details</p>
                            <hr style={{ margin: "0px", marginBottom: "3px", height: "0.6px", borderColor: "#021323" }} />
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="phone" id="phone" value={this.state.phone} required onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Phone</label>
                                <div className="form-helper helper_hide" id="phone_helper">
                                    Required
                                </div>
                            </div>
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="landline" id="landline" value={this.state.landline} required onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Landline Phone</label>
                                <div className="form-helper helper_hide" id="landline_helper">
                                    Required
                                </div>
                            </div>
                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="fax" id="fax" value={this.state.fax} required onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Fax</label>
                                <div className="form-helper helper_hide" id="fax_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input className="did-floating-input" type="email" placeholder=" " name="email" id="email" value={this.state.email} required onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Email Address</label>
                                <div className="form-helper helper_hide" id="email_helper">
                                    Required
                                </div>
                            </div>

                            <p style={{ margin: "0px", fontSize: "15px" }}>Contact Details</p>
                            <hr style={{ margin: "0px", marginBottom: "3px", height: "0.6px", borderColor: "#021323" }} />

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="contact_person"
                                    id="contact_person"
                                    required
                                    value={this.state.contact_person}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Contact Person</label>
                                <div className="form-helper helper_hide" id="contact_person_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="contact_mobile_number"
                                    id="contact_mobile_number"
                                    required
                                    value={this.state.contact_mobile_number}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Mobile Number</label>
                                <div className="form-helper helper_hide" id="contact_mobile_number_helper">
                                    Required
                                </div>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="designation"
                                    id="designation"
                                    value={this.state.designation}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Designation</label>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input
                                    className="did-floating-input"
                                    type="text"
                                    placeholder=" "
                                    name="contact_person_email"
                                    id="contact_person_email"
                                    required
                                    value={this.state.contact_person_email}
                                    onChange={ChangeHandler.bind(this)}
                                    onClick={ChangeHandler.bind(this)}
                                />
                                <label className="did-floating-label">Email Address</label>
                                <div className="form-helper helper_hide" id="contact_person_email">
                                    Required
                                </div>
                            </div>

                            <p style={{ margin: "0px", fontSize: "15px" }}>Register Address Details</p>
                            <hr style={{ margin: "0px", marginBottom: "3px", height: "0.6px", borderColor: "#021323" }} />

                            <div className="col-lg-6 col-md-6 col-sm-6 did-floating-label-content">
                                <textarea className="did-floating-input" type="text" placeholder=" " cols="60" rows="2" name="address_1" id="address_1" required value={this.state.address_1} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Address 1</label>
                                <div className="form-helper helper_hide" id="address_1_helper">Required</div>
                            </div>

                            <div className="col-lg-6 col-md-6 col-sm-6 did-floating-label-content">
                                <textarea className="did-floating-input" type="text" placeholder=" " cols="60" rows="2" name="address_2" id="address_2" value={this.state.address_2} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Address 2</label>
                            </div>

                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="city" id="city" value={this.state.city} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">City</label>
                                <div className="form-helper helper_hide" id="city_helper">Required</div>
                            </div>
                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="state" id="state" required value={this.state.state} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">State</label>
                                <div className="form-helper helper_hide" id="state_helper">Required</div>
                            </div>
                            <div className="col-lg-3 col-md-3 col-sm-3 did-floating-label-content">
                                <input className="did-floating-input" type="text" placeholder=" " name="zip_code" id="zip_code" required value={this.state.zip_code} onChange={ChangeHandler.bind(this)} onClick={ChangeHandler.bind(this)} />
                                <label className="did-floating-label">Zip Code</label>
                                <div className="form-helper helper_hide" id="zip_code_helper">Required</div>
                            </div>

                            <SubmitButton form_type={form_type} list_page={List_Path}></SubmitButton>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default CreateUpdatePage;
